import React from 'react';
import { useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import AddPhrase from '../../components/AddPhrase';
import PhraseList from '../../components/PhraseList';
import { addPhrase, getPhrases } from '../../phraseService';

const useStyles = makeStyles((theme) => ({
  body: {
    padding: theme.spacing(1, 0, 0, 0),
  },
  form: {
    width: 1000,
    padding: theme.spacing(0, 0, 0, 2),
  },
  input: {
    padding: theme.spacing(1, 0),
    margin: theme.spacing(0.2, 0, 0, 0),
  },
}));

export default function MainPage() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const addPhraseAction = async (data) => {
    await dispatch(addPhrase(data));
    return dispatch(getPhrases({ limit: 9 }));
  };

  return (
    <Grid
      container
      direction="row"
      justify="flex-start"
      alignItems="flex-start"
      className={classes.body}
    >
      <Grid container item xs={12} className={classes.form}>
        <AddPhrase addPhrase={addPhraseAction} />
      </Grid>
      <Grid container item xs={12} className={classes.form}>
        <PhraseList limit="9" />
      </Grid>
    </Grid>
  );
}
